import React, {useState} from 'react'
import { Wheel } from "react-custom-roulette";
import {Button, TextField, Box} from "@mui/material";
import Confetti from 'react-confetti'
import {useWindowSize} from "react-use";
import styled from 'styled-components'


const number = [0.5]
// const mColor = ['#f5d5d3','#faf9d7', '#ebfad7', '#ffdc9c', '#4287f5', '#f54299', '#fc5659']
const mColor = ['#f5d5d3', '#fcba03']
function Food({food, onRemove, dataLength}){
    const {option, id} = food;
    return (
        <div style={{display:"flex", justifyContent:"space-between", marginBottom:"2%"}}>
            <b className={"text-white"}>{option}</b>
            <Button color={"warning"} size={"small"} variant={"contained"} disabled={dataLength < 2} onClick={() => onRemove(id)}>삭제</Button>
        </div>
    )
}

    function MyWheel({data,option, onChange, onCreate, onRemove}){
        const [prizeNumber, setPrizeNumber] = useState(0);
        const [mustSpin, setMustSpin] = useState(false);

        const getRandomColor = () => {
            mColor.push("#"+Math.floor(Math.random() * 16777215).toString(16))
        }

        //roulette spin
        const handleSpinClick = () => {
            const newPrizeNumber = Math.floor(Math.random() * data.length)
            setPrizeNumber(newPrizeNumber)
            setMustSpin(true)
        }
    return(
        <div style={{alignItems: "center", display:"flex", justifyContent: "center", flexDirection: "column"}}>
            <div style={{display: "flex", width:"60vw", justifyContent: "space-between"}}>
                <div></div>
            <div style={{marginLeft:"13vw"}}>
                <Wheel
                    style={{width:"100px"}}
                mustStartSpinning={mustSpin}
                prizeNumber={prizeNumber}
                data={data}
                spinDuration={number}
                onStopSpinning={() => {
                    setMustSpin(false);
                }}
                backgroundColors={mColor}
                textColors={['#060606']}
            />
            </div>
                {/*<Confetti width={width} height={height} />*/}
            <div style={{border:"1px solid white", borderRadius:"5px", width:"20%", height:"25vw", overflow:"auto" }}>
                <span className={"text-white"}>메뉴 리스트</span>
                <div >
                    {
                        data.map(
                            food => (<Food food={food} key={food.id} dataLength={data.length} onRemove={onRemove}/>)
                        )
                    }
                </div>
            </div>
            </div>
            <div style={{display: "flex", flexDirection: "column",justifyContent: "center", width:"10%"}}>
            <Button variant="contained" color="primary" onClick={handleSpinClick}>spin</Button>
            <Box>
                <TextField
                    variant="outlined"
                    color="warning"
                    label="음식입력"
                    name="option"
                    onChange={onChange}
                    value={option}
                    style={{backgroundColor: "white", borderRadius: "5px", marginTop: "5%", marginBottom:"5%"}}
                />
            </Box>
                {/*<Button variant={"contained"} color={"primary"} onClick={getRandomColor}>추가</Button>*/}
            <Button variant="contained" color="primary" style={{marginBottom: "5%"}}
                    onClick={() => {
                        onCreate();
                        getRandomColor();
                    }}>등록</Button>
            <span className="text-white">{!mustSpin ? data[prizeNumber].option : "두근두근"}</span>
            </div>
        </div>
    )
    }

export default MyWheel